import uniGrid from "@/components/uni-grid/uni-grid.vue"
import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
import uniFav from '@/components/uni-fav/uni-fav.vue'
// 没有更多组件
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
// 通告
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue'
import {mapState} from 'vuex'

export default {
	components: {uniGrid, uniGridItem, uniFav, uniLoadMore, uniNoticeBar},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	data(){
		return {
			// 轮播图
			background: ['color1', 'color2', 'color3'],
			            indicatorDots: true,
			            autoplay: true,
			            interval: 2000,
			            duration: 500,
						// 宫格列表
						list: [{
								// url: '/static/img/home.png',
								text: 'Grid 1',
								badge: '0',
								type: "primary"
							},
							{
								// url: '/static/c2.png',
								text: 'Grid 2',
								badge: '1',
								type: "success"
							},
							{
								// url: '/static/c3.png',
								text: 'Grid 3',
								badge: '99',
								type: "warning"
							},
							{
								// url: '/static/c4.png',
								text: 'Grid 4',
								badge: '2',
								type: "error"
							},
							{
								// url: '/static/c5.png',
								text: 'Grid 5'
							},
							{
								// url: '/static/c6.png',
								text: 'Grid 6'
							},
							{
								// url: '/static/c7.png',
								text: 'Grid 7'
							},
							{
								// url: '/static/c8.png',
								text: 'Grid 8'
							},
							{
								// url: '/static/c9.png',
								text: 'Grid 9'
							}
						]
		}
	},
	onLoad() {
		console.log("1111")
		setTimeout(function () {
		            console.log('start pulldown');
					console.log("22222")
		        }, 1000);
		        //uni.startPullDownRefresh();
		        uni.startPullDownRefresh({
		            success:function(res){
		                console.log(res);  //success 返回参数说明
		            }
		        }); 
				uni.getSystemInfo({  
				        success:function(e){  
				            // Vue.prototype.statusBar = e.statusBarHeight  







				




				

				            Vue.prototype.customBar = e.statusBarHeight + e.titleBarHeight  

				        }  
				    }) 
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
	},
	onPullDownRefresh() {
	        console.log('refresh');
			console.log("111111111111")
	        setTimeout(function () {
	            uni.stopPullDownRefresh();  //停止动画
	        }, 1000);
	    },
		methods: {
		        changeIndicatorDots(e) {
		            this.indicatorDots = !this.indicatorDots
		        },
		        changeAutoplay(e) {
		            this.autoplay = !this.autoplay
		        },
		        intervalChange(e) {
		            this.interval = e.target.value
		        },
		        durationChange(e) {
		            this.duration = e.target.value
		        },
				change(e) {
					let {
						index
					} = e.detail
					this.list[index].badge && this.list[index].badge++
					console.log(index)
					// 判断index跳转对应页面
					if(index == 0){
					uni.navigateTo({
					    url: '../griditem/griditem1'
					})}
					if(index == 1){
					uni.navigateTo({
					    url: '../griditem/griditem2'
					})}
					if(index == 2){
					uni.navigateTo({
					    url: '../griditem/griditem3'
					})}
					if(index == 3){
					uni.navigateTo({
					    url: '../griditem/griditem4'
					})}
					if(index == 4){
					uni.navigateTo({
					    url: '../griditem/griditem5'
					})}
					if(index == 5){
					uni.navigateTo({
					    url: '../griditem/griditem6'
					})}
					if(index == 6){
					uni.navigateTo({
					    url: '../griditem/griditem7'
					})}
					if(index == 7){
					uni.navigateTo({
					    url: '../griditem/griditem8'
					})}
					if(index == 8){
					uni.navigateTo({
					    url: '../griditem/griditem9'
					})}
					
	
					// }
				// 显示点击宫格index
					uni.showToast({
						title: `点击第${index+1}个宫格`,
						icon: 'none'
					})
				},
		    }
}
